<h1 id="数据驱动和模型驱动" tabindex="-1"><a class="header-anchor" href="#数据驱动和模型驱动" aria-hidden="true">#</a> 数据驱动和模型驱动</h1>
<p>深度学习使得大家思考方式从模型驱动到数据驱动</p>
<h2 id="数据驱动" tabindex="-1"><a class="header-anchor" href="#数据驱动" aria-hidden="true">#</a> 数据驱动</h2>
<p>是指在得到一组数据（数据很庞大），使用模型去匹配数据，让模型去贴合数据，从而改变该模型，达到效果。</p>
<p>经典案例：工作流引擎</p>
<h2 id="模型驱动" tabindex="-1"><a class="header-anchor" href="#模型驱动" aria-hidden="true">#</a> 模型驱动</h2>
<p>模型定义好了，往模型里填充数据，得到模型想要的结果。</p>
<p>经典案例：模板引擎，代码生成器</p>
<h2 id="区别" tabindex="-1"><a class="header-anchor" href="#区别" aria-hidden="true">#</a> 区别</h2>
<p>数据驱动是改变模型，模型驱动是改变数据</p>
